.feature-card {
  height: 330px;
  width: 280px;
  position: relative;
  overflow: hidden;
  margin-top: 20px;
  border: 1px solid black;
  border-radius: 10px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.9);
  background-color: transparent;
  transition: 1s;
}

.feature-card::before,
.feature-card::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  transition: -webkit-transform 1s ease;
  transition: transform 1s ease;
  transition: transform 1s ease, -webkit-transform 1s ease;
}

.feature-card::before {
  top: 0;
  left: 0;
  border-radius: 10px;
  background-image: linear-gradient(
    to bottom right,
    #dfd00a,
    #7f0404 70%
  ); /* Top-left section color */
  -webkit-transform: translateY(-100%) translateX(-100%);
          transform: translateY(-100%) translateX(-100%);
}

.feature-card::after {
  bottom: 0;
  right: 0;
  border-radius: 10px;
  background-image: linear-gradient(
    to bottom right,
    #dfd00a,
    #7f0404 70%
  ); /* Bottom-right section color */
  -webkit-transform: translateY(100%) translateX(100%);
          transform: translateY(100%) translateX(100%);
}

.feature-card:hover::before {
  -webkit-transform: translateY(0) translateX(0);
          transform: translateY(0) translateX(0);
}
.feature-card:hover::after {
  -webkit-transform: translateY(0) translateX(0);
          transform: translateY(0) translateX(0);
}

.feature-wrapper {
  position: relative;
  width: 100px;
  height: 100px;
  top: 30px;
  margin: auto;
}

.feature-box {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100px;
  height: 100px;
  border: 5px solid white;
  transition: -webkit-transform 1s ease;
  transition: transform 1s ease;
  transition: transform 1s ease, -webkit-transform 1s ease; /* Add transition for smooth rotation */
}

.feature-card:hover .feature-box {
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg); /* Rotate the box on hover */
  /* border-color: white; */
}

.feature-icon {
  z-index: 2;
  font-size: 70px;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 50%;
  left: 50%;
  color: white;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  transition: 1s;
}
/* .feature-card:hover .feature-icon {
  color: white;
} */

.feature-title {
  position: absolute;
  top: 150px;
  left: 50%; /* Position the left edge at the center */
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%); /* Move the text back by half of its width */
  z-index: 2;
  font-family: Serif;
  font-size: 22px;
  font-weight: bold;
  color: white;
  transition: 1s;
  text-align: center; /* Center-align the text content */
  width: 280px; /* Optional: Set a width if desired */
}

/* .feature-card:hover .feature-title {
